	module.exports = {"data":{"post":{"timeToRead":1,"canonicalURL":"https://blog.iansinnott.com/the-wp_post-object/","html":"<p>This is a quick reference for myself and anyone else who commonly uses the WP_Post object in WordPress. I find that I often forget exactly what attributes this object has so I finally decided to just make a post about it and save my future self having to dump the var to remember. Here it is:</p>\n<!--more-->\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>WP_Post Object\n(\n  [ID] => 252\n  [post_author] => 81\n  [post_date] => 2013-06-13 09:27:19\n  [post\\_date\\_gmt] => 2013-06-13 09:27:19\n  [post_content] =>\n  [post_title] => Mac Mall, 1be/5ba\n  [post_excerpt] =>\n  [post_status] => publish\n  [comment_status] => open\n  [ping_status] => open\n  [post_password] =>\n  [post_name] => mac-mall-1be5ba\n  [to_ping] =>\n  [pinged] =>\n  [post_modified] => 2013-06-13 09:27:19\n  [post\\_modified\\_gmt] => 2013-06-13 09:27:19\n  [post\\_content\\_filtered] =>\n  [post_parent] => 0\n  [guid] => http://orangefinders.dev/listing/mac-mall-1be5ba/\n  [menu_order] => 0\n  [post_type] => listing\n  [post\\_mime\\_type] =>\n  [comment_count] => 0\n  [filter] => raw\n)</code></pre>\n      </div>\n<p>This is the result of calling <code>print_r()</code> on a <code>WP_Post</code> object. Enjoy.</p>","frontmatter":{"created":"2013-06-15T07:00:00.000Z","title":"The WP_Post Object","disqusId":"1402742612"}}},"pathContext":{"id":"/Users/ian/dev/sites/blog.iansinnott.com/content/_posts/2013-06-15-the-wp_post-object.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"Learn Vim & Code Like a Boss","created":"2013-08-09T07:00:00.000Z"},"slug":"learn-vim-code-like-a-boss"},"prev":{"frontmatter":{"title":"WordPress Development Server - Full Set Up Guide","created":"2013-05-30T07:00:00.000Z"},"slug":"wordpress-development-server-full-set-up-guide"}}}

